"use client";
// types.ts
interface StudentDetails {
  id: string;
  name: string;
  subjects: string[];
  marks: number[];
}

// StudentManagement.tsx
import React, { useState, ChangeEvent } from "react";

const StudentManagement: React.FC = () => {
  // Status Form State
  const [statusFormData, setStatusFormData] = useState({
    studentId: "",
    studentName: "",
    isPassed: false,
  });

  // Marks Form State
  const [marksFormData, setMarksFormData] = useState({
    studentId: "",
    subject: "Math",
    marks: "",
  });

  // Details Form State
  const [detailsFormData, setDetailsFormData] = useState({
    studentId: "",
    studentName: "",
  });

  const [studentDetails, setStudentDetails] = useState<StudentDetails | null>(
    null
  );
  const [error, setError] = useState<string>("");
  const [success, setSuccess] = useState<string>("");

  const subjects: string[] = ["Math", "Science", "English"];

  // Status Form Handlers
  const handleStatusIdChange = (e: ChangeEvent<HTMLInputElement>): void => {
    const value = e.target.value;
    if (!value || /^\d+$/.test(value)) {
      setStatusFormData((prev) => ({ ...prev, studentId: value }));
    }
  };

  const handleStatusNameChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setStatusFormData((prev) => ({ ...prev, studentName: e.target.value }));
  };

  const handleStatusPassedChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setStatusFormData((prev) => ({ ...prev, isPassed: e.target.checked }));
  };

  // Marks Form Handlers
  const handleMarksIdChange = (e: ChangeEvent<HTMLInputElement>): void => {
    const value = e.target.value;
    if (!value || /^\d+$/.test(value)) {
      setMarksFormData((prev) => ({ ...prev, studentId: value }));
    }
  };

  const handleSubjectChange = (e: ChangeEvent<HTMLSelectElement>): void => {
    setMarksFormData((prev) => ({ ...prev, subject: e.target.value }));
  };

  const handleMarksChange = (e: ChangeEvent<HTMLInputElement>): void => {
    const value = e.target.value;
    if (!value || (parseInt(value) >= 0 && parseInt(value) <= 100)) {
      setMarksFormData((prev) => ({ ...prev, marks: value }));
    }
  };

  // Details Form Handlers
  const handleDetailsIdChange = (e: ChangeEvent<HTMLInputElement>): void => {
    const value = e.target.value;
    if (!value || /^\d+$/.test(value)) {
      setDetailsFormData((prev) => ({ ...prev, studentId: value }));
    }
  };

  const handleDetailsNameChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setDetailsFormData((prev) => ({ ...prev, studentName: e.target.value }));
  };

  // Form Submit Handlers
  const handleInsertStatus = async (): Promise<void> => {
    try {
      if (!statusFormData.studentId || !statusFormData.studentName) {
        throw new Error("Student ID and Name are required");
      }
      setError("");
      setSuccess("Web3 integration required: Would call insertStudentStatus()");
    } catch (err) {
      setError(
        err instanceof Error ? err.message : "An unknown error occurred"
      );
    }
  };

  const handleInsertMarks = async (): Promise<void> => {
    try {
      if (!marksFormData.studentId || !marksFormData.marks) {
        throw new Error("Student ID and Marks are required");
      }
      if (
        parseInt(marksFormData.marks) < 0 ||
        parseInt(marksFormData.marks) > 100
      ) {
        throw new Error("Marks must be between 0 and 100");
      }
      setError("");
      setSuccess("Web3 integration required: Would call insertStudentMarks()");
    } catch (err) {
      setError(
        err instanceof Error ? err.message : "An unknown error occurred"
      );
    }
  };

  const handleGetDetails = async (): Promise<void> => {
    try {
      if (!detailsFormData.studentId || !detailsFormData.studentName) {
        throw new Error("Student ID and Name are required");
      }
      setError("");
      setSuccess("Web3 integration required: Would call getStudentDetails()");
    } catch (err) {
      setError(
        err instanceof Error ? err.message : "An unknown error occurred"
      );
    }
  };

  return (
    <div className="container mx-auto p-4 space-y-6">
      <h1 className="text-3xl font-bold mb-6">Student Management System</h1>

      {error && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
          {error}
        </div>
      )}

      {success && (
        <div className="bg-green-100 border border-green-400 text-green-700 px-4 py-3 rounded">
          {success}
        </div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Insert Student Status */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-semibold mb-4">Insert Student Status</h2>
          <div className="space-y-4">
            <input
              type="number"
              placeholder="Student ID"
              className="w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              value={statusFormData.studentId}
              onChange={handleStatusIdChange}
            />
            <input
              type="text"
              placeholder="Student Name"
              className="w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              value={statusFormData.studentName}
              onChange={handleStatusNameChange}
            />
            <div className="flex items-center space-x-2">
              <label className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  checked={statusFormData.isPassed}
                  onChange={handleStatusPassedChange}
                  className="h-4 w-4"
                />
                <span>Passed</span>
              </label>
            </div>
            <button
              onClick={handleInsertStatus}
              className="w-full bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              Insert Status
            </button>
          </div>
        </div>

        {/* Insert Student Marks */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-semibold mb-4">Insert Student Marks</h2>
          <div className="space-y-4">
            <input
              type="number"
              placeholder="Student ID"
              className="w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              value={marksFormData.studentId}
              onChange={handleMarksIdChange}
            />
            <select
              value={marksFormData.subject}
              onChange={handleSubjectChange}
              className="w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              {subjects.map((sub) => (
                <option key={sub} value={sub}>
                  {sub}
                </option>
              ))}
            </select>
            <input
              type="number"
              placeholder="Marks (0-100)"
              min="0"
              max="100"
              className="w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              value={marksFormData.marks}
              onChange={handleMarksChange}
            />
            <button
              onClick={handleInsertMarks}
              className="w-full bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              Insert Marks
            </button>
          </div>
        </div>

        {/* Get Student Details */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-semibold mb-4">Get Student Details</h2>
          <div className="space-y-4">
            <input
              type="number"
              placeholder="Student ID"
              className="w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              value={detailsFormData.studentId}
              onChange={handleDetailsIdChange}
            />
            <input
              type="text"
              placeholder="Student Name"
              className="w-full p-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              value={detailsFormData.studentName}
              onChange={handleDetailsNameChange}
            />
            <button
              onClick={handleGetDetails}
              className="w-full bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              Get Details
            </button>

            {studentDetails && (
              <div className="mt-4 p-4 border rounded">
                <h3 className="font-bold">Student Details:</h3>
                <p>ID: {studentDetails.id}</p>
                <p>Name: {studentDetails.name}</p>
                <div className="mt-2">
                  <h4 className="font-semibold">Marks:</h4>
                  {studentDetails.subjects.map((sub, index) => (
                    <p key={sub}>
                      {sub}: {studentDetails.marks[index]}
                    </p>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default StudentManagement;
